import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { ProbabilityGuru } from './probabability_guru.js';
import { data_dictionary } from './data_import.js';
import { Spring } from 'react-spring';

var nations_data = data_dictionary['nations'];
var probabilities_dict = data_dictionary['probabilities_dict'];
var player_permutations_dict = data_dictionary['player_permutations_dict'];


var rosso_corsa = "#D30208";
var crayola_gold = "#E5C685";
var antique_white = "#F5EED5";
var chinese_black = "#171714";
var midnight_blue = "#015386";
var honolulu_blue = "#0074B1";
var light_honolulu_blue = "#99C7E0";
var grass_green = "#30AB4E";

var participants = ['France', 'Argentina', 'Portugal', 'Uruguay', 'Brazil', 'Mexico',
  'Belgium', 'Japan', 'Spain', 'Russia', 'Croatia', 'Denmark',
  'Sweden', 'Switzerland', 'Colombia', 'England'];

var prob_guru = new ProbabilityGuru(participants, probabilities_dict);


function build_tournament(participants) {
  var tournament = {};
  tournament.rounds = [];
  for (let i = 0; i < Math.sqrt(participants.length) + 1; i++) {
    tournament.rounds.push(build_round(i, participants));
  }
  tournament.num_rounds = tournament.rounds.length;
  return tournament;
}

//Just assuming a clean power of 2 for the tournament for now
function build_round(round_number, participants) {
  let num_matches = participants.length / Math.pow(2, round_number + 1);
  let round = {};
  round.id = "r" + round_number;
  round.matches = [];
  let round_stride = Math.pow(2, round_number)
  for (let i = 0; i < num_matches; i++) {
    let match = {};
    match.id = round.id + "_" + i;
    match.round_id = round.id;
    match.p1 = participants.slice(i * 2 * round_stride, i * 2 * round_stride + round_stride);
    match.p2 = participants.slice(round_stride + i * 2 * round_stride, round_stride + i * 2 * round_stride + round_stride);

    round.matches.push(match);
  }
  return round;
}

class PlayerBox extends Component {

  constructor(props) {
    super(props);
    this.onClicked = this.onClicked.bind(this);
  }

  onClicked = (e) => {
    this.props.onChange(this.props.player_id, this.props.round_id, this.props.fixed)
    console.log("Clicked player: " + this.props.player_id);
  }

  render() {
    let winner = prob_guru.isWinner(this.props.round_id, this.props.player_id);
    let prob = this.props.prob;

    let colour = this.props.fixed ? light_honolulu_blue : "lightgray";
    colour = winner ? rosso_corsa : colour;
    let fontStyle = (winner || prob===1.0) ? "bold" : "normal";
    let style = { y: this.props.y };

    return (
      <Spring to={style} key={this.props.player_id}>
              {({ y }) => (
                <div key={this.props.player_id} onClick={this.onClicked}
                  style={{  fontFamily: 'Quicksand',
                            fontWeight: fontStyle,
                            cursor: "pointer", 
                            backgroundColor: colour, 
                            margin: 2,
                            opacity: 0.5 + prob*0.5,
                            position:"relative",
                            transform: `translate3d(0, ${y}px, 0)`,
                          }}>
                {this.props.player_id} {prob < 1.0 ? " - " + (prob * 100).toFixed(1) + "%" : ""}
                </div>
              )}
    </Spring> );
      /*<div key={this.props.player_id} onClick={this.onClicked}
          style={{  fontFamily: 'Quicksand',
                    fontWeight: fontStyle,
                    cursor: "pointer", 
                    backgroundColor: colour, 
                    margin: 2,
                    opacity: 0.5 + prob*0.5,
                    position:"relative",
                    transform: `translate3d(0, ${this.props.y}px, 0)`,
                  }}>
        {this.props.player_id} {prob < 1.0 ? " - " + (prob * 100).toFixed(1) + "%" : ""}
    </div>);*/
  }

}

class MatchBox extends Component {

  constructor(props) {
    super(props);
    this.players = props.players;
    this.round_id = props.round_id;
    this.id = props.id;
  }

  render() {
    let player_data = [];
    this.players.forEach(e => {
      player_data.push({player_id:e, prob: prob_guru.getRoundProbability(participants, e, this.props.round_id) })
    });

    let sorted = player_data.slice();
    sorted.sort( (a,b) =>{ return b.prob - a.prob; });
    var el = document.getElementById('dummyforfontsize');
    var style = window.getComputedStyle(el, null);//.getPropertyValue('font-size');
    var fontSize = parseFloat(style.height.slice(0,-2)) + parseFloat(style.marginTop.slice(0,-2));// + parseFloat(style.marginBottom.slice(0,-2));//parseFloat(style)+6; 
    console.log("h: " + fontSize);
    return (
      <div style={{ margin: 5, height:player_data.length*fontSize }}>
        {
          player_data.map((e, i) => {
            return (<PlayerBox key={e.player_id} prob={e.prob} y={-i*fontSize + sorted.indexOf(e)*fontSize} round_id={this.round_id} player_id={e.player_id} index={i} fixed={prob_guru.isWinner(this.round_id, e.player_id)} onChange={this.props.playerClick} />);
          })
        }
      </div>
    )
  }
}

class RoundColumn extends Component {

  constructor(props) {
    super(props);
    this.round_data = props.round_data;
  }


  addMatchBox(match) {
    let firstGroup = <MatchBox key="p1" id="p2" players={match.p1} round_id={match.round_id} playerClick={this.props.playerClick} />;
    let vs = match.p2.length > 0 ? <h4 style={{ margin: 2 }}>vs</h4> : "";
    let secondGroup = <MatchBox key="p2" id="p2" players={match.p2} round_id={match.round_id} playerClick={this.props.playerClick} />;

    return (
      <div key={match.id} style={{ margin: 20, padding: 10, backgroundColor: honolulu_blue }}>
        {firstGroup}
        {vs}
        {secondGroup}
      </div>
    );
  }

  render() {
    let round_data = this.round_data;

    return (
      <div key={round_data.id} style={{ minWidth:"15%", margin: 20, float: "left", backgroundColor: "none", display:"table", verticalAlign:"middle"}}>
        <h2>{this.props.title}</h2>
        {//<div>{(prob_guru.getWinFlags(round_data.id) >>> 0).toString(2)}</div>
        //<div>{(prob_guru.getPossibilityMask(round_data.id) >>> 0).toString(2)}</div>
        }
        {
          round_data.matches.map((e, i) => {
            return (this.addMatchBox(e));
          })
        }
      </div>
    )
  }
}

class TournamentVis extends Component {

  constructor(props) {
    super(props);
    this.tournament_data = build_tournament(participants);
    this.fixed_wins = {};
    for (let i = 0; i < this.tournament_data.num_rounds; i++) {
      this.fixed_wins["r" + i] = 0x0;
    }
    this.playerClicked = this.playerClicked.bind(this);
  }

  playerClicked(player_id, round_id, fixed) {
    if (fixed) {
      this.fixed_wins[round_id] &= ~(1 << participants.indexOf(player_id));
      prob_guru.unsetPlayerAsWinner(round_id, player_id);
    } else {
      this.fixed_wins[round_id] |= (1 << participants.indexOf(player_id));
      prob_guru.setPlayerAsWinner(round_id, player_id);
    }
    console.log("Tournament got player clicked: " + player_id);
    this.forceUpdate();
  }



  render() {
    let titles = ["Round 1", "Quarter-Finals", "Semi-Finals", "Final", "Champion"];
    return (
      <div className="tournament">
        {this.tournament_data.rounds.map((e, i) => { return <RoundColumn title={titles[i]} key={i} round_data={e} playerClick={this.playerClicked} /> })}
      </div>
    );
  }
}


class App extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">World Cup Knockout Probability Explorer</h1>
        </header>
        <p className="App-intro">
          This page shows probabilities for stage participation and ultimate winner based on glicko ratings derived from World Cup competition performance. Click on a team box to set or unset the team as the winner of a match.
        </p>
        <TournamentVis />
      </div>
    );
  }
}

export default App;



