import { data_dictionary } from './data_import.js';

var player_permutations_dict = data_dictionary['player_permutations_dict'];

export class ProbabilityGuru {

    constructor(participants, probabilities_dict) {
        this.participants = participants;
        this.probabilities_dict = probabilities_dict;
        this.possibility_masks = {};
        this.win_flags = {};
        for (let i = 0; i < Math.log2(participants.length) + 1; i++) {
            this.possibility_masks["r" + i] = 0xffffffff;
            this.win_flags["r" + i] = 0;
        }
        this.perm_cache = {};
    }

    getPossibilityMask(round_id) {
        return this.possibility_masks[round_id];
    }

    getWinFlags(round_id) {
        return this.win_flags[round_id];
    }

    isWinner(round_id, player_id) {
        let player_index = this.participants.indexOf(player_id);
        return (this.win_flags[round_id] & (1 << player_index));
    }

    getBlockInfo = (round_id, player_id, full_match = false) => {
        let player_index = this.participants.indexOf(player_id);
        let round_num = this.roundNumFromID(round_id);
        let block_size = Math.pow(2, round_num) * (full_match ? 2 : 1);
        let block_shift = Math.floor(player_index / block_size) * block_size;
        let block_index = player_index % block_size;

        return {
            block_size: block_size,
            block_shift: block_shift,
            block_index: block_index
        };

    }

    setPlayerAsWinner = (round_id, player_id) => {
        let player_index = this.participants.indexOf(player_id);
        this.win_flags[round_id] = this.win_flags[round_id] | (1 << player_index)

        let round_num = this.roundNumFromID(round_id);
        let block_info = this.getBlockInfo(round_id, player_id, true);

        for (let i = block_info.block_shift; i < block_info.block_shift + block_info.block_size; i++) {
            if (i !== player_index)
                this.unsetPlayerAsWinner(round_id, this.participants[i], true, false);
        }

        if (round_num > 0) {
            this.setPlayerAsWinner("r" + (round_num - 1), player_id);
        }


        for (let i = round_num; i < round_num + 2; i++) {
            let rnd_id = "r" + i;
            block_info = this.getBlockInfo(rnd_id, player_id, false);
            let block = ~(0xffffffff << block_info.block_size);
            block = block << block_info.block_shift;
            block = ~block;
            this.possibility_masks[rnd_id] = this.possibility_masks[rnd_id] & block;
            this.possibility_masks[rnd_id] = this.possibility_masks[rnd_id] | ((1 << block_info.block_index) << block_info.block_shift);
        }

        for (let i = round_num + 2; i < 5; i++) {
            this.possibility_masks["r" + i] &= this.possibility_masks["r" + (round_num + 1)];
        }

        //console.log("player Toggle received by prob guru, round: " + round_id + ", player: " + player_id);
    }

    unsetPlayerAsWinner = (round_id, player_id, no_back = false, no_forward = false) => {
        if (!this.isWinner(round_id, player_id)) {
            return;
        }

        let player_index = this.participants.indexOf(player_id);
        this.win_flags[round_id] = this.win_flags[round_id] & ~(1 << player_index)

        let round_num = this.roundNumFromID(round_id);
        if (!no_forward && round_num < 4) {
            this.unsetPlayerAsWinner("r" + (round_num + 1), player_id, true, false);
        }
        if (!no_back && round_num > 0) {
            this.unsetPlayerAsWinner("r" + (round_num - 1), player_id, false, true);
        }


        let block_info = this.getBlockInfo(round_id, player_id, true);
        let block = ~(0xffffffff << block_info.block_size);
        block = block << block_info.block_shift;

        this.possibility_masks[round_id] = this.possibility_masks[round_id] | block;

        for (let i = round_num + 1; i < 5; i++) {
            this.possibility_masks["r" + i] |= (this.possibility_masks[round_id] & block);

            let curr_block_info = this.getBlockInfo("r" + i, player_id, true);
            let win_block = ~(0xffffffff << curr_block_info.block_size);
            win_block = win_block << curr_block_info.block_shift;

            if ((this.win_flags["r" + i] & win_block))
                break;
        }
        //console.log("player Toggle received by prob guru, round: " + round_id + ", player: " + player_id);
    }



    get_round_permutations = (num_players, round_num, combined = false) => {
        if (this.perm_cache["" + num_players + round_num + combined]) {
            return this.perm_cache["" + num_players + round_num + combined];
        }
        if (round_num < 1)
            return 1;
        let num_matches = num_players / Math.pow(2, round_num);
        if (combined) {
            let result = Math.pow(2, num_matches) * this.get_round_permutations(num_players, round_num - 1, true);
            this.perm_cache["" + num_players + round_num + combined] = result;
            return result;
        } else {
            let result = Math.pow(2, num_matches);
            this.perm_cache["" + num_players + round_num + combined] = result;
            return result;
        }
    }

    roundNumFromID(round_id) {
        return parseInt(round_id.slice(1), 10);
    }

    getBlockProbability = (participants, round_id, player_id) => {
        let block_info = this.getBlockInfo(round_id, player_id, false);
        let block_probability = 0.0;

        for (let i = block_info.block_shift; i < block_info.block_shift + block_info.block_size; i++) {
            block_probability += this.getRoundProbability(participants, participants[i], round_id, true);
        }
        return block_probability;
    }

    flagsToInt(round_num, flags) {
        let result = 0;
        let need_test = 0;
        let block_info = this.getBlockInfo("r" + round_num, "France", true);
        let block = Math.pow(2, block_info.block_size) - 1;

        for (let i = 0; i < 32; i += block_info.block_size) {
            let block_val = (flags & (block << i)) >> i;
            if (block_val > 0) {
                need_test += 1 << (i / block_info.block_size);
                if (block_val < Math.pow(2, block_info.block_size / 2)) {
                    result += 1 << (i / block_info.block_size);
                }
            }
        }
        return { need_test: need_test, flags: result };
    }

    getRoundProbability = (participants, player_id, round_id, no_block = false) => {
        let num_players = participants.length;
        let round_num = this.roundNumFromID(round_id);
        if (round_num < 1) {
            return 1.0;
        }

        let player_index = participants.indexOf(player_id);
        let probabilities = this.probabilities_dict["r" + (round_num - 1)];
        let poss_mask = this.possibility_masks["r" + round_num];
        let team_bitstrings = player_permutations_dict["r" + (round_num - 1)];

        var bit_mask = 0;
        var bit_val = 0;

        let r = round_num;

        while (r > 0) {
            let round_perms = this.get_round_permutations(num_players, r, false);
            let bit_length = Math.log2(round_perms);
            let team_poss_per_match = (num_players / bit_length);
            let perm_slot = player_index / team_poss_per_match;
            let high_low = (player_index % team_poss_per_match) < (team_poss_per_match * 0.25);
            bit_mask = (bit_mask << bit_length) | (1 << perm_slot);
            bit_val = (bit_val << bit_length) | ((1 & (high_low)) << perm_slot)
            r -= 1;
        }

        let total_probability = 0.0;
        let all_round_probability = 0.0;
        let permutations = this.get_round_permutations(num_players, round_num, true);
        let last_round_perms = this.get_round_permutations(num_players, round_num - 1, false);
        let test_flags = 0;
        let win_flags = 0;

        for (let i = 0; i < round_num; i++) {
            let flag_res = this.flagsToInt(i, this.win_flags["r" + i]);
            test_flags = test_flags << Math.log2(this.get_round_permutations(num_players, i + 1, false));
            win_flags = win_flags << Math.log2(this.get_round_permutations(num_players, i + 1, false));
            test_flags += flag_res.need_test;
            win_flags += flag_res.flags;
        }



        //if (round_num === 2) {
        //    console.log("win_flags: " + (win_flags >>> 0).toString(2));
        //    console.log("test_flags: " + (test_flags >>> 0).toString(2));
        //}
        for (let i = 0; i < permutations; i++) {
            //if( ((Math.floor(i/last_round_perms) & test_flags) ^ win_flags) === 0 ){
            if (((i & test_flags) ^ win_flags) === 0) {
                //if( round_num === 2 && player_id === "France"){
                //  console.log("Allowing permutation: " + i);
                //}
                //if( poss_mask & (1<<player_index) && team_bitstrings[i] & (1<<player_index)){
                //if (((poss_mask & team_bitstrings[i]) === team_bitstrings[i])) {
                all_round_probability += probabilities[i];
                if (team_bitstrings[i] & (1 << player_index)) {
                    total_probability += probabilities[i];
                }
            }
        }

        //return total_probability;// * this.getRoundProbability(participants, player_id, "r"+ (round_num-1));
        if (no_block)
            return total_probability;
        else
            return total_probability * 1.0 / all_round_probability;// * (1.0 / this.getBlockProbability(participants, round_id, player_id));

    }


}


