var data_dictionary = {};

export{ data_dictionary };

var nations_import = require('./data/nations_data.csv');
var probabilities_import = require('./data/probabilities.csv');
var player_permutations_import = require('./data/winners.csv');

var probabilities_dict = {};

probabilities_import.forEach( e => {
  probabilities_dict[e.round] = e.probs.split(',').map( (e,i) => { return parseFloat(e); });
});

var player_permutations_dict = {};

player_permutations_import.forEach( e => {
  player_permutations_dict[e.round] = e.winners.split(',').map( (e,i) => { return parseInt(e,10); });
});

var nations_data = {};

nations_import.forEach(element => {
  element.id = element.nation.replace(/\s/g, "");
  nations_data[element.nation] = {
    confederation: element.confederation,
    firsts: JSON.parse(element.firsts),
    seconds: JSON.parse(element.seconds),
    thirds: JSON.parse(element.thirds),
    fourths: JSON.parse(element.fourths)
  };
});


data_dictionary['nations'] = nations_data;
data_dictionary['probabilities_dict'] = probabilities_dict;
data_dictionary['player_permutations_dict'] = player_permutations_dict;




